<template>

    <v-container class="email-verification">
        <v-row>
            <v-col cols="12">
                <h2>{{verifyYourEmailText}}</h2>
            </v-col>
        </v-row>

        <v-col cols="12">
            <v-card>
                <v-progress-linear
                        value="100"
                        height="10"
                        color="blue-grey"
                        reactive
                        dark
                ></v-progress-linear>

                <v-container>
                    <v-row class="pa-3">
                        <v-col text-align="center">
                            <v-card-text class="pa-0" align="justify">
                                {{verificationEmailSentTo}}
                            </v-card-text>
                            <v-card-text class="pa-0" align="center">
                                <b>{{getEmail}}</b>
                            </v-card-text>
                        </v-col>
                    </v-row>

                    <v-row class="pa-3">
                        <v-col align="center">
                            <v-progress-circular
                                    size="100"
                                    color="primary"
                                    indeterminate
                            ></v-progress-circular>
                            <br>
                            <br>
                        </v-col>
                    </v-row>

                    <v-btn block color="primary" @click="sendUserEmailVerification" style="margin-top: 10px">
                        {{resendEmailText}}
                    </v-btn>
                </v-container>
            </v-card>

        </v-col>
        <v-row style="position: fixed; bottom: 10px; width: 100%">
            <v-col></v-col>
            <v-col align="center" class="pa-0"><a style="font-size: 12px" @click="wantToLogout">{{logOutText}}</a>
            </v-col>
            <v-col></v-col>
        </v-row>

        <v-dialog
                v-model="dialog"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <h3 style="margin-bottom: 10px">{{aboutToLogOutText}}</h3>
                    <p style="margin-bottom: 7px">{{askingForLogOutConfirmationText}}</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="dialog = false">{{cancelText}}</v-btn>
                    <v-btn color="primary" @click="logoutUser">{{confirmText}}</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "emailVerification",
        data() {
            return {
                dialog: false,
                sendEmailButtonIsDisabled: true,
                logOutText: Constants.TEXT_TO_LOG_OUT,
                snackbar: false,
                snackbarText: '',
                isEmailVerified: false,
                resendEmailText: Constants.TEXT_RESEND_EMAIL,
                intervalCheckEmailVerified: null,
                closeText: Constants.TEXT_CLOSE,
                cancelText: Constants.TEXT_CANCEL,
                toLogOutText: Constants.TEXT_TO_LOG_OUT,
                confirmText: Constants.TEXT_CONFIRM,
                aboutToLogOutText: Constants.TEXT_YOU_ARE_ABOUT_TO_LOG_OUT,
                verificationEmailSentTo: Constants.TEXT_VERIFICATION_EMAIL_SENT_TO,
                askingForLogOutConfirmationText: Constants.TEXT_ARE_YOU_SURE_YOU_WANT_TO_LOG_OUT,
                verifyYourEmailText: Constants.TEXT_VERIFY_YOUR_EMAIL,
                verificationEmailSentText: Constants.TEXT_VERIFICATION_EMAIL_SENT
            }
        },
        methods: {
            ...mapActions('user', ['logout', 'sendEmailVerification', 'checkIfEmailIsVerified', 'checkRegistrationState']),
            sendUserEmailVerification: function () {
                //Send verification email
                let self = this;
                let resultSendEmailVerification = this.sendEmailVerification();

                resultSendEmailVerification.then(() => {
                    self.snackbarText = self.verificationEmailSentText;
                    self.snackbar = true;
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            wantToLogout() {
                let self = this;
                self.dialog = true;
            },
            logoutUser() {
                let resultLogout = this.logout();

                resultLogout.then(() => {
                    clearInterval(this.intervalCheckEmailVerified);
                    this.$router.replace('home');
                })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            },
            checkEmailVerified: function () {
                let self = this;
                let resultCheckIfEmailIsVerified = this.checkIfEmailIsVerified();

                resultCheckIfEmailIsVerified
                    .then((isVerified) => {
                        if (isVerified) {
                            clearInterval(this.intervalCheckEmailVerified);

                            let resultCheckRegistrationState = self.checkRegistrationState(self.$store.getters['user/getProfile']);

                            resultCheckRegistrationState
                                .then((goal) => {
                                    self.$router.replace(goal);
                                })
                                .catch((error) => {
                                    self.snackbarText = error;
                                    self.snackbar = true;
                                });
                        }
                    })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });

            }
        },
        beforeMount() {
            this.sendEmailVerification().catch((error) => {
                if (error != Constants.ERROR_TOO_MANY_REQUESTS) {
                    self.snackbarText = error;
                    self.snackbar = true;
                }
            });
            this.intervalCheckEmailVerified = setInterval(this.checkEmailVerified, 2000);
        },
        computed: {
            ...mapGetters('user', ['getEmail']),
        }
    }
</script>

<style scoped>
</style>
